import React, { useState, useEffect } from 'react';
import * as sendbirdService from '../services/sendbirdService';
import './ChannelList.css';

export interface Channel {
  url: string;
  name: string;
  memberCount: number;
  unreadMessageCount?: number;
  lastMessage?: string;
}

interface ChannelListProps {
  onChannelSelect: (channel: Channel) => void;
  selectedChannelUrl?: string;
  onClose?: () => void;
}

const ChannelList: React.FC<ChannelListProps> = ({ onChannelSelect, selectedChannelUrl, onClose }) => {
  const [channels, setChannels] = useState<Channel[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [newChannelName, setNewChannelName] = useState('');
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 480);

  // Определение мобильного режима
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 480);
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Загрузка списка каналов
  const loadChannels = async () => {
    setLoading(true);
    setError(null);
    
    try {
      const channelList = await sendbirdService.getChannelList();
      setChannels(channelList);
    } catch (err: any) {
      console.error('Ошибка при загрузке списка каналов:', err);
      setError('Не удалось загрузить список чатов. ' + (err.message || ''));
    } finally {
      setLoading(false);
    }
  };

  // Загрузка списка каналов при монтировании компонента
  useEffect(() => {
    loadChannels();
  }, []);

  // Создание нового канала
  const handleCreateChannel = async () => {
    if (!newChannelName.trim()) return;
    
    try {
      const newChannel = await sendbirdService.createChannel(newChannelName);
      setChannels(prev => [...prev, newChannel]);
      setShowCreateModal(false);
      setNewChannelName('');
      
      // Автоматически выбираем новый канал
      onChannelSelect(newChannel);
    } catch (err: any) {
      console.error('Ошибка при создании канала:', err);
      setError('Не удалось создать чат. ' + (err.message || ''));
    }
  };

  // Обновление списка каналов
  const handleRefresh = () => {
    loadChannels();
  };

  return (
    <div className="channel-list-container">
      <div className="channel-list-header">
        {isMobile && onClose && (
          <button 
            className="close-channel-list" 
            onClick={onClose} 
            title="Закрыть список чатов"
          >
            ✕
          </button>
        )}
        <h2>Чаты</h2>
        <div className="channel-list-actions">
          <button onClick={handleRefresh} className="refresh-button" title="Обновить список">
            🔄
          </button>
          <button onClick={() => setShowCreateModal(true)} className="new-channel-button">
            Новый чат
          </button>
        </div>
      </div>
      
      {error && <div className="channel-list-error">{error}</div>}
      
      <div className="channel-list">
        {loading ? (
          <div className="channel-list-loading">Загрузка чатов...</div>
        ) : channels.length === 0 ? (
          <div className="channel-list-empty">
            <p>Нет доступных чатов</p>
            <button onClick={() => setShowCreateModal(true)} className="create-first-channel">
              Создать первый чат
            </button>
          </div>
        ) : (
          channels.map(channel => (
            <div
              key={channel.url}
              className={`channel-item ${channel.url === selectedChannelUrl ? 'selected' : ''}`}
              onClick={() => onChannelSelect(channel)}
            >
              <div className="channel-name">{channel.name}</div>
              <div className="channel-meta">
                <span className="channel-members">{channel.memberCount} участников</span>
                {channel.unreadMessageCount && channel.unreadMessageCount > 0 && (
                  <span className="unread-badge">{channel.unreadMessageCount}</span>
                )}
              </div>
              {channel.lastMessage && (
                <div className="channel-last-message">{channel.lastMessage}</div>
              )}
            </div>
          ))
        )}
      </div>
      
      {/* Модальное окно для создания нового канала */}
      {showCreateModal && (
        <div className="create-channel-modal-overlay">
          <div className="create-channel-modal">
            <h3>Создать новый чат</h3>
            <input
              type="text"
              value={newChannelName}
              onChange={(e) => setNewChannelName(e.target.value)}
              placeholder="Название чата"
              className="new-channel-input"
              autoFocus
            />
            <div className="create-channel-actions">
              <button
                className="cancel-button"
                onClick={() => {
                  setShowCreateModal(false);
                  setNewChannelName('');
                }}
              >
                Отмена
              </button>
              <button
                className="create-button"
                onClick={handleCreateChannel}
                disabled={!newChannelName.trim()}
              >
                Создать
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ChannelList; 